<template>
  <div class="spyj-page">
    <headerBar :header-data="headerData" />
    <!-- 流程信息 begin -->
    <div class="fixed-project">
      <div class="list">
        <h3>{{ dbxq.lcslmc }}</h3>
        <div class="info">
          <span>
            <i class="iconfont iconlianxiren" />
            {{ dbxq.preUsers }}
          </span>
          <span>
            <i class="iconfont iconshijian" />
            {{ dbxq.showTime }}
          </span>
          <span>
            <i class="iconfont iconpingbiaobaogao" />
            {{ dbxq.rwmc }}
          </span>
        </div>
      </div>
    </div>
    <!-- 流程信息 end -->
    <div class="container-mini">
      <!-- 下一步任务信息 begin -->
      <div v-if="submitResultMap.submitTaskList!=null && submitResultMap.submitTaskList.length>0">
        <div class="content">
          <div class="common-main box">
            <!-- 通用标题 beign -->
            <div class="title">
              <div class="title-left">
                <h2>下一步任务</h2>
              </div>
            </div>
            <!-- 通用标题 end -->
            <div
              v-for="(submitTask, index) in submitResultMap.submitTaskList"
              :key="index"
            >
              <!-- 子流程 -->
              <div v-if="submitTask.isZlc">
                子流程
              </div>
              <!-- 非子流程 -->
              <div v-else>
                <!-- 任务生成自定义 -->
                <div v-if="submitTask.generatorid!=null">
                  任务生成自定义
                </div>
                <!-- 有人、不可选 -->
                <div v-else-if="submitTask.isCanChoose===2 && submitTask.actors!=null && submitTask.actors.length>0" class="page-list">
                  <div class="pagelist-left">{{ submitTask.name }}</div>
                  <div class="pagelist-right">{{ submitTask.detail }}</div>
                </div>
                <!-- 有人、可选 -->
                <div v-else-if="submitTask.actors!=null && submitTask.actors.length>0">
                  <van-cell :title="submitTask.name" :value="submitTask.xzActorsVal" is-link @click="showXzActorsPopup(index,0)" />
                  <!-- 选择人员弹出层 beign -->
                  <van-popup
                    v-model="submitTask.showXzActors"
                    position="bottom"
                    closeable
                    :style="{ height: '40%' }"
                  >
                    <div class="content">
                      <div
                        v-for="(ry, ryIndex) in submitTask.actors"
                        :key="ryIndex"
                      >
                        <div :class="ry.isActive ? 'active select-renyuan': 'select-renyuan'" @click="selectRy(ry)">
                          <div class="renyuan-l">
                            <i class="iconfont iconxuanzhong" />
                          </div>
                          <div class="renyuan-m">
                            <div class="name">{{ ry.name }}</div>
                            <div class="info"><span>公司:{{ ry.orgName }}</span><span>部门:{{ ry.deptName }}</span></div>
                          </div>
                        </div>
                      </div>
                    </div>

                    <div class="common-space" />
                    <!-- 底部按钮 begin -->
                    <div class="foot-btn fixed">
                      <ul>
                        <li><van-button type="info" round size="large" @click="xzActorsOnConfirm">确定</van-button></li>
                      </ul>
                    </div>
                    <!-- 底部按钮 end -->
                  </van-popup>
                  <!-- 选择人员弹出层 end -->
                </div>
                <!-- 指定人员 -->
                <div v-else>
                  <van-cell :title="submitTask.name" :value="submitTask.xzActorsVal" is-link @click="showXzActorsPopup(index,1)" />
                </div>
              </div>
              <!-- 并行任务 -->
              <div v-if="submitTask.preTaskList!=null && submitTask.preTaskList.length>0">
                <van-notice-bar
                  wrapable
                  :scrollable="false"
                  :text="'未提交的并行任务:'+submitTask.bxrwString"
                />
              </div>
            </div>
          </div>
        </div>
      </div>
      <div v-else-if="rwlx!=7 && rwlx!=-1">
        <van-notice-bar
          :scrollable="false"
          text="提交之后，流程将结束。"
        />
      </div>
      <!-- 下一步任务信息 end -->

      <!-- 管理任务信息 begin -->
      <div v-if="submitResultMap.managingTaskList!=null && submitResultMap.managingTaskList.length>0">
        <div class="content">
          <div class="common-main box">
            <!-- 通用标题 beign -->
            <div class="title">
              <div class="title-left">
                <h2>管理任务</h2>
              </div>
            </div>
            <!-- 通用标题 end -->
            <div
              v-for="(managingTask, index) in submitResultMap.managingTaskList"
              :key="index"
            >
              <van-cell :title="managingTask.name" :value="managingTask.xzActorsVal" is-link @click="showXzActorsPopup(index,2)" />
            </div>
          </div>
        </div>
      </div>
      <!-- 管理任务信息 end -->

      <div v-if="dbxq.tjyjbt!=null && dbxq.tjyjbt!=3" class="content">
        <div class="common-main box">
          <div class="txyj">
            <div class="txyj-tit">
              <span v-if="dbxq.tjyjbt!=null && dbxq.tjyjbt===1" class="text-danger">*</span>
              {{ dbxq.taskCommentLabel }}
              <div class="label-text info">同意</div>
              <span class="more" @click="showCyyPopup()">选择常用语
                <i class="iconfont iconarrowright" />
              </span>
              <!--              <span class="more" @click="saveCyy()">保存常用语-->
              <!--              </span>-->
            </div>
            <div class="txyj-content">
              <textarea v-model="spyj" rows="3" :placeholder="'请输入'+dbxq.taskCommentLabel" />
              <div v-if="submitResultMap.zxrString && submitResultMap.zxrString!==''">
                当前任务是多人并行提交，{{ submitResultMap.zxrString }}还未提交，将在其余人员提交后进入下一步。
              </div>
            </div>
          </div>
        </div>
      </div>
      <div v-else-if="submitResultMap.zxrString && submitResultMap.zxrString!==''" class="content">
        <div class="common-main box">
          当前任务是多人并行提交，{{ submitResultMap.zxrString }}还未提交，将在其余人员提交后进入下一步。
        </div>
      </div>

      <div class="common-space" />
      <div class="foot-btn fixed">
        <ul>
          <li><van-button type="info" round size="large" @click="subimitSPYJ()">提交</van-button></li>
        </ul>
      </div>
    </div>

    <transition name="router-slid" mode="out-in">
      <router-view />
    </transition>

    <!-- 选择常用语popup beign -->
    <van-popup
      v-model="showCyy"
      position="bottom"
      round
    >
      <van-picker
        show-toolbar
        :columns="cyyList"
        value-key="nr"
        @cancel="showCyy = false"
        @confirm="xzCyyOnConfirm"
      />
    </van-popup>
    <!-- 选择常用语popup end -->

    <!-- 选择人员弹出层 beign -->
    <van-popup
      v-model="showXzRy"
      closeable
      position="bottom"
      :style="{ height: '100%' }"
      @close="closePoPup()"
    >
      <van-index-bar :index-list="indexList">
        <div class="content">
          <van-sticky class="van-sticky-approve">
            <div class="close-button" @click="closePoPup()">
              <i class="iconfont iconguanbi" />
            </div>

            <div class="select-search">
              <van-search v-model="searchVal" placeholder="请输入搜索关键词" />
              <van-button plain type="info" size="small" @click="searchRy()">搜索</van-button>
            </div>
            <!--  已选人员 beign -->
            <div class="selected-renyuan">
              <dl>
                <dt>已选</dt>
                <dd>
                  <span
                    v-for="(yxry, yxryIndex) in yxryList"
                    :key="yxryIndex"
                    class="item"
                  >{{ yxry.xm }}</span>
                </dd>
              </dl>
            </div>
            <!--  已选人员 end -->
          </van-sticky>
          <common-list
            ref="commonlist"
            class="common-list"
            :post-url="list.postUrl"
            :req-data="list.reqData"
            :data-name="list.dataName"
            @returnData="getPrizeLog($event)"
          >
            <div
              v-for="(item, xsIndex) in list.items"
              :key="xsIndex"
            >
              <div v-if="item['xs'+item.ryxs]">
                <van-index-anchor :index="item['xs'+item.ryxs]" />
              </div>
              <div v-else>
                <div class="select-renyuan" :class="{ 'active': item.isActive }" @click="selectRy(item)">
                  <div class="renyuan-l">
                    <i class="iconfont iconxuanzhong" />
                  </div>
                  <div class="renyuan-m">
                    <div class="name">{{ item.xm }}</div>
                    <div class="info"><span>公司:{{ item.zzmc }}</span><span>部门:{{ item.bmmc }}</span></div>
                  </div>
                </div>
              </div>
            </div>
          </common-list>
        </div>

        <div class="common-space" />
        <!-- 底部按钮 begin -->
        <div class="foot-btn fixed">
          <ul>
            <li><van-button type="info" round size="large" @click="xzMutilActorsOnConfirm">确定</van-button></li>
          </ul>
        </div>
        <!-- 底部按钮 end -->
      </van-index-bar>
    </van-popup>
    <!-- 选择人员弹出层 end -->
    <van-dialog
      v-model="show3"
      title="审批成功！"
      confirm-button-text="返回待办"
      @confirm="spwc()"
    />
  </div>
</template>

<script>
import headerBar from '@/components/header/headerBar'
import commonList from '@/components/list/commonList.vue'
import { mapState, mapMutations } from 'vuex'
import { getSubmitTaskListAction } from '../../../store/jbr/dbsx'
import { pageRouter } from '@/common/js/mixin.js'

export default {
  name: 'DbsxSubmit',
  components: {
    headerBar,
    commonList
  },
  mixins: [pageRouter],
  data() {
    return {
      show3: false,
      submitResultMap: {},
      spyj: '',
      dbxq: {},
      headerData: {
        title: '待办审批',
        leftUrl: 'back',
        leftText: ' '
      },
      dqindex: -1,
      yxryList: [],
      ryMap: {},
      searchVal: '',
      showCyy: false,
      showXzRy: false,
      cyyList: [],
      pageno: 1, // 页
      totalpageno: 0, // 总页数
      prizeLog: [],
      indexList: [],
      list: {
        items: [],
        error: false, // 是否在刷新
        postUrl: '',
        reqData: '',
        dataName: '',
        noDataText: ''
      },
      rwlx: -1,
      dqtype: -1,
      buttonState: true
    }
  },
  computed: {
    ...mapState({
      // listItem: state => state.jbrdbsx.listItem,
      txBizid: state => state.jbrdbsx.txBizid,
      spxz: state => state.jbrdbsx.spxz,
      userInfo: state => state.mh.mhUserInfo
    })

  },
  // watch: {
  //   // // 检测搜索条件发生变化，并进行搜索
  //   cyy(newQuery) {
  //     // this.search(newQuery)
  //     this.spyj = newQuery
  //   }
  // },
  created() {
    this.initTaskData()
    this.getDbxq()
    this.getcyylist()
    this.getrylist()
  },
  methods: {
    ...mapMutations([
      'jbrdbsx/SET_DBSX_CYY',
      'jbrdbsx/SET_Refresh'
    ]),
    initTaskData() {
      this.getSubmitTaskList()
    },
    getDbxq() {
      this.$store.dispatch('jbrdbsx/getDbxqAction', {
        bizid: this.txBizid // 单点登录传过来的执行人id
      }).then(res => {
        this.dbxq = res
      }).catch(() => {
      })
    },
    getPrizeLog(data) {
      this.list.items = data
      for (let i = 0; i < this.list.items.length; i++) {
        const ryxs = this.list.items[i].ryxs
        if (this.list.items[i]['xs' + ryxs] && this.indexList.indexOf(this.list.items[i]['xs' + ryxs]) === -1) {
          this.indexList.push(this.list.items[i]['xs' + ryxs])
        }
        for (let j = 0; j < this.yxryList.length; j++) {
          if (this.list.items[i].id === this.yxryList[j].id) {
            this.list.items[i].isActive = true
          }
        }
      }
    },
    getrylist() {
      this.list.postUrl = 'jbrdbsx/getRyListAction'
      this.list.reqData = { dqbmid: this.userInfo.bmid }
      this.list.dataName = 'ryList'
      this.list.noDataText = '暂无可选人员'
    },
    searchRy() {
      this.indexList = []
      const query = {
        rymc: this.searchVal
      }
      this.$refs.commonlist.onRefresh(query)
      // this.$bus.$emit('refList', this.searchVal)
      // this.$refs.commlist.testNum(9809)
    },
    getSubmitTaskList() {
      getSubmitTaskListAction({
        id: this.txBizid
      }).then(res => {
        for (let i = 0; i < res.submitTaskList.length; i++) {
          res.submitTaskList[i].xzActorsVal = '选择人员'
          res.submitTaskList[i].showXzActors = false
          res.submitTaskList[i].bxrwString = ''
          res.submitTaskList[i].yxryList = []
          if (res.submitTaskList[i].preTaskList) {
            for (let j = 0; j < res.submitTaskList[i].preTaskList.length; j++) {
              if (j === res.submitTaskList[i].preTaskList.length - 1) {
                res.submitTaskList[i].bxrwString += res.submitTaskList[i].preTaskList[j].name
              } else {
                res.submitTaskList[i].bxrwString += res.submitTaskList[i].preTaskList[j].name + ','
              }
            }
          }
          if (res.submitTaskList[i].actors) {
            for (let j = 0; j < res.submitTaskList[i].actors.length; j++) {
              res.submitTaskList[i].actors[j].isActive = false
            }
          }
        }
        if (res.managingTaskList) {
          for (let i = 0; i < res.managingTaskList.length; i++) {
            res.managingTaskList[i].xzActorsVal = '选择人员'
          }
        }
        this.submitResultMap = res
        this.rwlx = this.submitResultMap.wf_rwsl.rwlx
      })
    },
    xzActorsOnConfirm() {
      let xzActorsId = ''
      let xzActorsVal = ''
      let xzActorsDeptid = ''
      let xzActorsDeptName = ''
      let xzActorsOrgid = ''
      const yxrylist = this.submitResultMap.submitTaskList[this.dqindex].yxryList
      if (yxrylist) {
        for (let i = 0; i < yxrylist.length; i++) {
          if (i === yxrylist.length - 1) {
            xzActorsId += yxrylist[i].id
            xzActorsVal += yxrylist[i].name
            xzActorsDeptid += yxrylist[i].deptid
            xzActorsDeptName += yxrylist[i].deptName
            xzActorsOrgid += yxrylist[i].orgid
          } else {
            xzActorsId += yxrylist[i].id + ','
            xzActorsVal += yxrylist[i].name + ','
            xzActorsDeptid += yxrylist[i].deptid + ','
            xzActorsDeptName += yxrylist[i].deptName + ','
            xzActorsOrgid += yxrylist[i].orgid + ','
          }
        }
      }
      this.submitResultMap.submitTaskList[this.dqindex].xzActorsId = xzActorsId
      if (xzActorsVal === '') {
        this.submitResultMap.submitTaskList[this.dqindex].xzActorsVal = '选择人员'
      } else {
        this.submitResultMap.submitTaskList[this.dqindex].xzActorsVal = xzActorsVal
      }
      this.submitResultMap.submitTaskList[this.dqindex].xzActorsDeptid = xzActorsDeptid
      this.submitResultMap.submitTaskList[this.dqindex].xzActorsDeptName = xzActorsDeptName
      this.submitResultMap.submitTaskList[this.dqindex].xzActorsOrgid = xzActorsOrgid
      this.submitResultMap.submitTaskList[this.dqindex].showXzActors = false
    },
    xzMutilActorsOnConfirm() {
      let xzActorsId = ''
      let xzActorsVal = ''
      let xzActorsDeptid = ''
      let xzActorsDeptName = ''
      let xzActorsOrgid = ''
      const yxrylist = this.yxryList
      if (yxrylist) {
        for (let i = 0; i < yxrylist.length; i++) {
          if (i === yxrylist.length - 1) {
            xzActorsId += yxrylist[i].id
            xzActorsVal += yxrylist[i].xm
            xzActorsDeptid += yxrylist[i].bmid
            xzActorsDeptName += yxrylist[i].bmmc
            xzActorsOrgid += yxrylist[i].zzid
          } else {
            xzActorsId += yxrylist[i].id + ','
            xzActorsVal += yxrylist[i].xm + ','
            xzActorsDeptid += yxrylist[i].bmid + ','
            xzActorsDeptName += yxrylist[i].bmmc + ','
            xzActorsOrgid += yxrylist[i].zzid + ','
          }
        }
      }
      if (this.dqtype === 1) {
        this.submitResultMap.submitTaskList[this.dqindex].yxrylist = yxrylist
        this.submitResultMap.submitTaskList[this.dqindex].xzActorsId = xzActorsId
        if (xzActorsVal === '') {
          this.submitResultMap.submitTaskList[this.dqindex].xzActorsVal = '选择人员'
        } else {
          this.submitResultMap.submitTaskList[this.dqindex].xzActorsVal = xzActorsVal
        }
        this.submitResultMap.submitTaskList[this.dqindex].xzActorsDeptid = xzActorsDeptid
        this.submitResultMap.submitTaskList[this.dqindex].xzActorsDeptName = xzActorsDeptName
        this.submitResultMap.submitTaskList[this.dqindex].xzActorsOrgid = xzActorsOrgid
      } else if (this.dqtype === 2) {
        this.submitResultMap.managingTaskList[this.dqindex].yxrylist = yxrylist
        this.submitResultMap.managingTaskList[this.dqindex].xzActorsId = xzActorsId
        if (xzActorsVal === '') {
          this.submitResultMap.managingTaskList[this.dqindex].xzActorsVal = '选择人员'
        } else {
          this.submitResultMap.managingTaskList[this.dqindex].xzActorsVal = xzActorsVal
        }
        this.submitResultMap.managingTaskList[this.dqindex].xzActorsDeptid = xzActorsDeptid
        this.submitResultMap.managingTaskList[this.dqindex].xzActorsDeptName = xzActorsDeptName
        this.submitResultMap.managingTaskList[this.dqindex].xzActorsOrgid = xzActorsOrgid
      }
      this.showXzRy = false
      if (this.searchVal !== '') {
        this.searchVal = ''
        this.searchRy()
      }
    },
    xzCyyOnConfirm(value) {
      this.spyj = value.nr
      this.showCyy = false
    },
    selectRy(ry) {
      if (this.dqtype === 0) {
        if (ry.isActive) {
          ry.isActive = false
          const index = this.submitResultMap.submitTaskList[this.dqindex].yxryList.findIndex(item => {
            if (item.id === ry.id) {
              return true
            }
          })
          this.submitResultMap.submitTaskList[this.dqindex].yxryList.splice(index, 1)
        } else {
          ry.isActive = true
          this.submitResultMap.submitTaskList[this.dqindex].yxryList.push(ry)
        }
      } else {
        if (ry.isActive) {
          ry.isActive = false
          const index = this.yxryList.findIndex(item => {
            if (item.id === ry.id) {
              return true
            }
          })
          this.yxryList.splice(index, 1)
        } else {
          ry.isActive = true
          this.yxryList.push(ry)
        }
      }
    },
    subimitSPYJ() {
      if (!this.buttonState) {
        return
      }
      this.clickSP()
    },
    clickSP() {
      this.buttonState = false
      if (this.spyj) {
        this.spyj = this.spyj.trim()
      }
      let ryxzValidate = true
      let spyjValidate = true
      for (let i = 0; i < this.submitResultMap.submitTaskList.length; i++) {
        if (this.submitResultMap.submitTaskList[i].xzActorsVal === '选择人员' &&
                (this.submitResultMap.submitTaskList[i].isCanChoose !== 2 || this.submitResultMap.submitTaskList[i].actors === null || this.submitResultMap.submitTaskList[i].actors.length === 0)) {
          ryxzValidate = false
          break
        }
      }
      if (this.submitResultMap.managingTaskList) {
        for (let i = 0; i < this.submitResultMap.managingTaskList.length; i++) {
          if (this.submitResultMap.managingTaskList[i].xzActorsVal === '选择人员') {
            ryxzValidate = false
            break
          }
        }
      }
      if (this.dbxq.tjyjbt != null && this.dbxq.tjyjbt === 1) {
        if (this.spyj === null || this.spyj.length === 0) {
          spyjValidate = false
        }
      }
      if (spyjValidate && ryxzValidate) {
        // this.$toast.success('审批成功！')
        // setTimeout(() => {
        // this.$router.push('/DbsxIndex')
        // this.SET_Refresh('true')
        // }, 2000)
        let xzryJson = ''
        for (let i = 0; i < this.submitResultMap.submitTaskList.length; i++) {
          if (this.submitResultMap.submitTaskList[i].xzActorsId) {
            const ryxx = "{taskid:'" + this.submitResultMap.submitTaskList[i].id +
                    "',ryid:'" + this.submitResultMap.submitTaskList[i].xzActorsId +
                    "',rymc:'" + this.submitResultMap.submitTaskList[i].xzActorsVal +
                    "',bmid:'" + this.submitResultMap.submitTaskList[i].xzActorsDeptid +
                    "',bmmc:'" + this.submitResultMap.submitTaskList[i].xzActorsDeptName +
                    "',zzid:'" + this.submitResultMap.submitTaskList[i].xzActorsOrgid + "'}"
            if (i === (this.submitResultMap.submitTaskList.length - 1)) {
              xzryJson += ryxx
            } else {
              xzryJson += ryxx + ','
            }
          }
        }
        xzryJson = '[' + xzryJson + ']'

        let manageXzryJson = ''
        if (this.submitResultMap.managingTaskList) {
          for (let i = 0; i < this.submitResultMap.managingTaskList.length; i++) {
            if (this.submitResultMap.managingTaskList[i].xzActorsId) {
              const manageryxx = "{taskid:'" + this.submitResultMap.managingTaskList[i].id +
                      "',ryids:'" + this.submitResultMap.managingTaskList[i].xzActorsId +
                      "',sfzx:'" + this.submitResultMap.managingTaskList[i].isExecute + "'}"
              if (i === (this.submitResultMap.managingTaskList.length - 1)) {
                manageXzryJson += manageryxx
              } else {
                manageXzryJson += manageryxx + ','
              }
            }
          }
        }
        manageXzryJson = '[' + manageXzryJson + ']'
        this.$store.dispatch('jbrdbsx/sendSP', {
          id: this.txBizid,
          taskComment: this.spyj,
          ryid: xzryJson,
          manageryid: manageXzryJson,
          dqjgid: this.userInfo.zzid,
          dqryid: this.userInfo.id
        }).then(res => {
          // this.$toast('审批成功！')
          this.show3 = true

          // this.SET_Refresh('true')
        }).catch(error => {
          this.buttonState = true
          // this.pageStyles = 3
          this.$toast(error)
        })
      } else if (!ryxzValidate) {
        this.buttonState = true
        this.$toast('请选择人员')
      } else {
        this.buttonState = true
        this.$toast('请设置审批意见')
      }
    },
    spwc() {
      this.$router.push({ name: 'DbsxIndex' })
    },
    getcyylist() {
      this.$store.dispatch('jbrdbsx/getCyyListAction', {
        pageno: this.pageno,
        id: this.userInfo.id
      }).then(res => {
        this.cyyList = res.data
        this.pageno = res.pageno
        this.totalpageno = res.totalpageno
      }).catch(() => {
      })
    },
    saveCyy() {
      if (this.spyj != null && this.spyj.length !== 0) {
        this.$store.dispatch('jbrdbsx/saveCyyAction', {
          nr: this.spyj,
          ryid: this.userInfo.id
        }).then(res => {
          this.$toast('保存成功！')
          this.getcyylist()
        }).catch(() => {
        })
      } else {
        this.$toast(this.dbxq.taskCommentLabel + '为空，不能保存为常用语!')
      }
    },
    showCyyPopup() {
      if (this.cyyList.length === 0) {
        this.$toast.fail('常用语为空，请手动输入')
      } else {
        this.showCyy = true
      }
    },
    closePoPup() {
      if (this.searchVal !== '') {
        this.searchVal = ''
        this.searchRy()
      }
      this.showXzRy = false
    },
    showXzActorsPopup(index, type) {
      this.dqindex = index
      this.dqtype = type
      if (type === 0) {
        this.submitResultMap.submitTaskList[this.dqindex].showXzActors = true
      } else if (type === 1) {
        if (this.submitResultMap.submitTaskList[this.dqindex].yxrylist != null &&
                this.submitResultMap.submitTaskList[this.dqindex].yxrylist.length > 0) {
          this.yxryList = this.submitResultMap.submitTaskList[this.dqindex].yxrylist
          for (let i = 0; i < this.list.items.length; i++) {
            this.list.items[i].isActive = false
            for (let j = 0; j < this.yxryList.length; j++) {
              if (this.list.items[i].id === this.yxryList[j].id) {
                this.list.items[i].isActive = true
              }
            }
          }
        } else {
          for (let i = 0; i < this.list.items.length; i++) {
            this.list.items[i].isActive = false
          }
          this.yxryList = []
        }
        this.showXzRy = true
      } else if (type === 2) {
        if (this.submitResultMap.managingTaskList[this.dqindex].yxrylist != null &&
                this.submitResultMap.managingTaskList[this.dqindex].yxrylist.length > 0) {
          this.yxryList = this.submitResultMap.managingTaskList[this.dqindex].yxrylist
          for (let i = 0; i < this.list.items.length; i++) {
            this.list.items[i].isActive = false
            for (let j = 0; j < this.yxryList.length; j++) {
              if (this.list.items[i].id === this.yxryList[j].id) {
                this.list.items[i].isActive = true
              }
            }
          }
        } else {
          for (let i = 0; i < this.list.items.length; i++) {
            this.list.items[i].isActive = false
          }
          this.yxryList = []
        }
        this.showXzRy = true
      }
    }
    // popupClick() {},
    // showPopUp(type) {
    //   this.$set(this.isPopupShow, type, true)
    // },
    // hidePopUp(type) {
    //   this.$set(this.isPopupShow, type, false)
    // }
  }

}

</script>

